.header {
  font-size: 17px;
  margin: 10px;
}

.header > div:nth-child(2) {
  margin-left: 20px;
}

.hamtainer {
  width: 40px;
  height: 40px;
  margin: 20px;
  cursor: pointer;
  overflow: visible;
}

.hamtainer svg {
  overflow: visible;
}

.hamtainer line {
  transition: transform 1s ease-in-out;
}

.hamtainer.open line:nth-child(1) {
  transform: translate(18%, -1%) rotate(36deg);
}

.hamtainer.open line:nth-child(2) {
  transform: rotate(-35deg) translate(-16%, 44%);
}

.hamtainer.open line:nth-child(3) {
  transform: translate(-30px, 0px);
}

.mobile-menu {
  position: absolute;
  background: white;
  right: -100%;
  top: 0px;
  width: 50%;
  height: 100%;
  padding: 30px;
  padding-top: 100px;
  transition: all 0.5s;
  min-height: 400px;
}

.mobile-menu.open {
  right: 0%;
}

.link {
  cursor: pointer;
  font-size: 14px;
  padding: 10px;
}

.active {
  color: blue;
}

img {
  width: 80%;
  margin: auto;
  margin-bottom: 20px;
  display: block;
}
